#footer{
    background: white;
    height: 100%;
    width: 100%;
}

/*Footer Top*/
#footer .top{
    height: 60vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}


/* Footer Center */
#footer .footer_center{
    height: 20vh;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr
}

#footer .footer_center .left{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

#footer .footer_center .left p{
    color: black;
}

#footer .footer_center .left .social_media{
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}

#footer .footer_center .left .social_media hr{
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    border: 1px rgb(5, 5, 5) solid;
}

#footer .footer_center .left .social_media a{
    color: rgb(0, 0, 0);
    margin: 1.5vw;
}

#footer .footer_center .left a .fab:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    color: rgb(89, 112, 148);
}


#footer .footer_center .left a .fab{
    -webkit-transition: -webkit-transform 0.5s ease-in-out;
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}

#footer .footer_center .center {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: end;
}

#footer .footer_center .center h3{
    font-size: 15px;
    -webkit-writing-mode: vertical-rl;
            writing-mode: vertical-rl;
    color: black;
}
#footer .footer_center .center li{
    list-style: none;
}

#footer .footer_center .center li a{
    color: black;
    
}

#footer .footer_center .center li a:hover{
    color: rgb(89, 112, 148);
}


#footer .footer_center .right {
    display: flex;
    align-items: center;
    justify-content: center;
}

#footer .footer_center .right a{
    text-decoration: none;
    color: black;
    font-size: 30px;
    
}

#footer .footer_center .right a:hover{
    color: rgb(89, 112, 148);
}

@media ( max-width: 530px){
    #footer .footer_center{
        grid-template-columns: 1fr 1fr
    }

    #footer .footer_center .right {
        display: none;
    }
}

@media ( max-height: 440px){
    #footer .footer_center{
        height: 40vh;
        grid-template-columns: 1fr 1fr
    }

    #footer .footer_center .right {
        display: none;
    }
}

/* bottom */
#footer .bottom{
    background: rgba(255, 220, 199, 0.74);
    height: 10vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#footer .bottom .footer_logo{
    font-size: 12px;
    margin: 0;
    color: black;
    text-decoration: none;
}

#footer .bottom a.footer_logo:hover{
    color: rgb(21, 21, 156);
}


#card {
    border-radius: 30px;
    width: 85%;
    height: 60vh;
    margin-bottom: 100px;
}

#card .content{
    width: 100%;
    height: 100%;
    text-align: center;
    position: relative;
    -webkit-transition: -webkit-transform 3s;
    transition: -webkit-transform 3s;
    transition: transform 3s;
    transition: transform 3s, -webkit-transform 3s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

#card:hover .content{
    -webkit-transform: rotateY(.5turn);
            transform: rotateY(.5turn);
}

#card .front,
#card .back {
    border-radius: 60px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    display: grid;
    align-content: center;
}

#card .front{
    -webkit-filter: blur(10);
            filter: blur(10);
    color: rgb(255, 251, 251);
    border: 1px black solid;
    -webkit-filter: contrast(120%);
            filter: contrast(120%);
}




#card .back{
    -webkit-transform: rotateY(.5turn);
            transform: rotateY(.5turn);
    background: rgb(0, 0, 0);
    color: rgb(181, 196, 219);
}

#card .back .description{
    font-size: 26px;
}

#card .title{
    position: relative;
    top: 160px;
    text-shadow: 1px 4px rgb(40, 49, 63);
    height: 100px;
    color: rgba(218, 187, 51, 0.9);
    -webkit-transform: translateZ(150px);
            transform: translateZ(150px);
    font-size: 2.3rem;
    background: -webkit-linear-gradient(left, rgba(0, 0, 0, 0.329) , rgba(0, 0, 0, 0.171));
    background: linear-gradient(to right, rgba(0, 0, 0, 0.329) , rgba(0, 0, 0, 0.171));
    border-bottom: 2px solid rgba(218, 187, 51, 0.9);
    border-radius: 50px;
}

#card .subtitle{
    -webkit-transform: translateZ(50px);
            transform: translateZ(50px)
}

#card .description{
    -webkit-transform: translateZ(100px);
            transform: translateZ(100px)
}
#contact_form{
    height: 80vh;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
}

#contact_form ul{
    list-style: none;
    padding:0;
}

#contact_form .brand{
    text-align: center;
}   


#contact_form .wrapper{
    box-shadow: 0 0 20px 0  rgba(76, 92, 117, 0.397);
}

#contact_form .wrapper > * {
    padding: 1em;
}

#contact_form .company{
    background: rgba(76, 92, 117, 0.452);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#contact_form .company ul a{
    text-decoration: none;
    color: rgb(181, 196, 219);
}

#contact_form .company ul a:hover{
    color: rgb(105, 149, 216);
}

#contact_form h3, .company ul {
    text-align: center;
    margin: 0 0 1rem 0;
}

#contact_form .contact{
    background: rgba(76, 92, 117, 0.308);
}

#contact_form form{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
}

#contact_form form label{
    display: block;
    font-size: 1rem;
}

#contact_form form p{
    margin: 0;
}

#contact_form form .full{
    grid-column: 1 / 3;
}

#contact_form form button, #contact_form form input, #contact_form form textarea{
    width: 100%;
    padding: 1em;
}
#contact_form form button{
    border-radius: 100px;
    color: rgb(181, 196, 219);
    background: rgb(40, 49, 63);
    border: 1px rgba(255, 255, 255, 0.5) solid;
    text-transform: uppercase;
    width: 70%;
}
#contact_form form .button{
    margin-left: 20%;
}

#contact_form form button:hover, #contact_form form button:focus{
    background: rgb(181, 196, 219);
    color: rgb(40, 49, 63);
    outline: 0;
    -webkit-transition: background-color 2s ease-out;
    transition: background-color 2s ease-out;
}

@media(min-width: 700px){
    #contact_form{
        width: 70%;
    }
    .wrapper{
        display: grid;
        grid-template-columns: 1fr 2fr;
    }
    .wrapper > *{
        padding: 2em;
    }

    .company h3, .company ul, .brad{
        text-align: left;
    }

}
