.nav {
  position: fixed;
  width: 100%;
  height: 16vh;
  background: rgba(0, 0, 0, 0.658);
  display: flex;
  align-items: center;
  z-index: 100;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  padding-bottom: 25px;
}

.nav hr {
  border: 0;
}

.nav .text {
  font-family: 'Laila';
  color: white;
  font-size: 20px;
  font-weight: 100;
}

.nav .social {
  grid-column: 1;
  grid-row: 2;
  position: relative;
  left: 0%;
}

.nav .social .fab {
  margin-left: 10%;
  color: white;
}

.nav .logo {
  grid-column: 1;
}

/* logo */
.nav .logo img {
  position: relative;
  left: 20%;
  top: 8%;
  height: 9vh;
}

.nav h1 {
  margin-right: 10px;
  font-family: 'Atifa';
}

/* list */
div.list {
  flex: 1 1;
  display: flex;
}

div.list ul {
  margin-left: auto;
  margin-right: 50px;
  list-style: none;
  display: flex;
}

div.list ul li {
  padding: 0 10px;
}

div.list ul li a {
  text-decoration: none;
  color: white;
}

div.li ul li a:hover,
div.li ul li a:active {
  color: #fa932f;
}

.nav .side_nav {
  display: none;
  margin-left: auto;
}

@media (max-width: 720px) {
  div.list {
    display: none;
  }
  .nav .side_nav {
    display: block;
  }
  .nav hr {
    border-top: 1px solid rgba(181, 196, 219, 0);
  }
}
